body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.navbar {
    top:-10%;
    width:100%;
    display:inline-block;
    background-color:white;
}

.navbar ul{
    list-style-type: none;
    margin:0px;
    padding:0px;
}

.navbar li:hover {
    color:white;
    background-color:rgb(51, 192, 126);
}

.navbar a {
    float:left;
    display:block;
    padding:8px;
    background-color:white;
    color:green;
    text-decoration: none;
}

.navbar a:hover {
    color:white;
    background-color:rgb(51, 192, 126);
}

.signup-btn {
    float:right;
}
.signup-top-div{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    min-height: 60vh;
}

/* .signup-tbl{
    display:block;
    background-color:whitesmoke; 
    min-width:300px;
    max-width:300px;
    text-align:left;

} */

.signup-tbl{
    padding: 20px;
    border: 2px solid rgb(41, 155, 102);
    border-radius:20px;
}
.submit-btn{
    margin-left:0%;
}

.signup-error-div{
    position: fixed;
    top:53%;
}
.login-tbl{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    min-height: 60vh;
}

input[type="text"],
input[type="password"]{

    border: 2px solid rgb(51, 192, 126);
    border-radius: 5px;

}

input[type="submit"]{

    margin-top: 10%;

    background-color:rgb(51, 192, 126);
    display:box;
    border:none;
    padding:5px;
    color:white;
}

.login-tbl input[type="submit"]{
    margin-left: -35%;

}

input[type="submit"]:hover{
    background-color:rgb(95, 235, 169);
    display:box;
    border:none;
    padding:5px;
    color:white;
}

.login-tbl tbody{
    padding: 20px;
    border: 2px solid rgb(41, 155, 102);
    border-radius:20px;
}

.error-div{
    color:red;
    position:fixed;
    top:49%;
}
.upload-form{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    min-height: 80vh;
}

.upload-tbl td,
.upload-tbl th
{
    vertical-align: top;
    padding: 5px;

}


.upload-tbl th
{
    text-align: left;
}

.dropzone{
    overflow-y: scroll;
    overflow-x: hidden;
    position: relative;
    width: 500px;
    height: 200px;
    border-width: 2px;
    border-color: rgb(51, 192, 126);
    border-style: dotted;
    border-radius: 5px;
}

.upload-tbl input[type="submit"]{
    margin-left:0 !important;
}

.upload-tbl input[type="text"]{
    width:99%;
    height:20px;
}
.file-box{
    display: inline-block;
    overflow: hidden;
    border: 1.5px dashed green;
    border-radius: 3px;
    width:100px;
    height:100px;
    font-family: "Gill Sans", inherit;
}

.file-box p{
    word-wrap: break-word;
    font-size:12px;
}
.allfiles-tbl-top-div{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    min-height: 60vh;
}

.tbl-container tr:nth-of-type(even){
    background-color:rgb(111, 206, 162);
    color:whitesmoke;
}


::-webkit-scrollbar-thumb{
background:rgb(51, 192, 126);
}

::-webkit-scrollbar{
width: 10px;
height: 10px;
}

::-webkit-scrollbar-track{
background-color:white;
}


.tbl-container thead,
.tbl-container tbody tr {
    display:table;
    width:600px;
    table-layout:fixed;
}

.tbl-container tbody{
    max-height: 300px;
    overflow-y: scroll;
    overflow-x: hidden;
    display: block;
}


.file-bar th,
.file-bar td{
    min-width:100px;
    max-width:100px;
    overflow:hidden;
}


.file-bar-property{
    min-width:100px;
    max-width:100px;
}
.download-btn{
    background-color:inherit;
    color:green;
    font-weight: bold;
    font-size:20px;
    border:none;
}



th.file-name-property,
td.file-name-property{
    min-width: 200px;
    max-width: 200px;
}
.footer{
    bottom:0px;
    width:100%;
    position:fixed;
    color:white;
    background-color:rgb(51, 192, 126);
}

.footer a{
    color:white;
}

.footer a:hover{
    color:#f8bb48;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

